import React from "react";
import Navbar from "../Navbar";
import Footer from "./Footer"

const Contact = () => {
  return (
    <>
    <div className="contactCircle"></div>
      <Navbar color = "green"/>
      <section className="contactUs">
        <div className="leftCon">
          <h1 className="playFair">Contact Us</h1>
          <p>
           Just submit your contact details and a short description of your project. And we'll schedule a call with you and your team at our earliest availability.
          </p>
        </div>
        <div className="rightCon">
        <div className="inputs">
          <input type="text" placeholder="Name" />
        </div>
        <div className="inputs">
          <input type="email" placeholder="Email" />
        </div>
        <div className="inputs">
          <input type="text" placeholder="Phone Number" />
        </div>
        <div className="inputs">
          <input type="text" placeholder="Message" />
        </div>
        <div className="inputs">
          <button className="contactBtn">Submit</button>
        </div>
        </div>
      </section>
      <Footer/>
    </>
  );
};

export default Contact;
